<template>
  <va-card
    class="card"
    :color="computedWidgetColor"
    :title="$t('domains.panel-title')"
  >
    <va-inner-loading :loading="loading">
      <div
        :style="{
          opacity: this.loading ? 0.2 : 1,
        }"
      >
        {{ $t('domains.subtitle1') }}
        <a
          href="https://ap.www.namecheap.com/domains/list/"
          :style="{ color: $themes.primary }"
          target="_blank"
          >Namecheap</a
        >
        {{ $t('domains.subtitle2') }}
        <br />
        <br />

        <div class="domains-title">
          <div class="domains-title_prop">{{ $t('domains.name') }}</div>
          <div class="domains-title_prop">{{ $t('domains.assigned') }}</div>
          <div class="domains-title_prop">{{ $t('domains.expiration') }}</div>
          <div class="domains-title_prop">{{ $t('domains.issue') }}</div>
        </div>
        <div class="domains-table">
          <div
            class="domains-table_item"
            v-for="domain in domains"
            :key="domain.ID"
          >
            <a
              :href="domain.Name"
              target="_blank"
              class="domains-table_item_prop center domain"
            >
              <span
                ><span class="suffix">https://www.</span>
                {{ domain.Name }}</span
              >
            </a>

            <div class="domains-table_item_prop">
              <va-select
                :options="managers"
                v-model="domain.Assigned"
                :value="domain.Assigned"
                @input="email => assignDomain(domain, email)"
                class="ma-0"
                no-clear
              />
            </div>
            <div class="domains-table_item_prop center">
              {{ getLifetime(domain) }}
            </div>
            <div
              class="domains-table_item_prop center"
              :class="{ clickable: !!checkIssue(domain) }"
            >
              <va-popover
                v-if="checkIssue(domain)"
                :icon="checkIssue(domain).icon"
                :color="checkIssue(domain).color"
                :title="checkIssue(domain).title"
                :message="checkIssue(domain).message"
                placement="right"
              >
                <span class="full-sized">
                  <va-icon name="fa fa-times" color="danger" />
                  {{ $t('domains.trouble') }}
                </span>
              </va-popover>
              <div v-else>
                <va-icon name="fa fa-check" color="success" />
                {{ $t('domains.good') }}
              </div>
            </div>
          </div>
        </div>
      </div>
    </va-inner-loading>
  </va-card>
</template>

<script>
import moment from 'moment'

import * as Domains from '@/api/Domains'
import * as Users from '@/api/Users'

export default {
  name: 'domains-panel',
  data: () => ({
    loading: true,
    popover: {
      title: 'Hey folks!',
      message: 'This tooltip is amazing:D',
      icon: {
        icon: 'fa fa-print',
        text: 'print',
      },
      color: 'warning',
    },
    managers: ['Nobody'],
    domains: [],
  }),
  computed: {
    computedWidgetColor() {
      return this.$themes.background
    },
  },
  methods: {
    assignDomain(domain, email) {
      this.loading = true
      Domains.assign({
        domain: domain.Name,
        email,
      }).then(({ data }) => {
        this.showToast(data.message, {
          icon: 'fa-check',
          position: 'bottom-center',
          duration: 4000,
          type: 'primary',
        })
        this.loading = false
      })
    },
    checkIssue(domain) {
      if (domain.IsLocked == 'true') {
        return {
          title: 'Validation',
          icon: 'fa fa-lock',
          color: 'danger',
          message: 'This domain is locked.',
        }
      } else if (domain.IsExpired == 'true') {
        return {
          title: 'Validation',
          icon: 'fa fa-calendar-times-o',
          color: 'danger',
          message: 'The domain was expired. Renew it in Namecheap.',
        }
      } else {
        return null
      }
    },
    getLifetime(d) {
      var startDate = moment()
      var endDate = moment(d.Expires, 'MM/DD/YYYY')

      var years = endDate.diff(startDate, 'years')
      var months = endDate.diff(startDate, 'months')
      var days = endDate.diff(startDate, 'days')
      var hours = endDate.diff(startDate, 'hours')
      var minutes = endDate.diff(startDate, 'minutes')
      var seconds = endDate.diff(startDate, 'seconds')

      if (years) {
        return years + ' years'
      } else if (months) {
        return months + ' months'
      } else if (days) {
        return days + ' days'
      } else if (hours) {
        return hours + ' hours'
      } else if (minutes) {
        return minutes + ' minutes'
      } else {
        return seconds + ' seconds'
      }
    },
  },
  async created() {
    Users.getManagers().then(({ data }) => {
      console.log(data)
      this.managers = data

      Domains.getList().then(({ data }) => {
        console.log(data)

        this.domains = data
        this.loading = false
      })
    })
  },
}
</script>

<style lang="scss">
.card {
  box-shadow: none;
  border-radius: 0.375rem;
}

.full-sized {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.suffix {
  opacity: 0.1;
  width: 0px;
  transition: all 0.5s ease;
}

.domains-table {
  display: flex;
  flex-direction: column;
  font-family: Montserrat;

  &_item {
    display: grid;
    height: 35px;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    background-color: #00000011;
    border-bottom: 1px solid #00000042;

    &_prop {
      width: 100%;
      height: 100%;
      text-align: center;
      overflow: hidden;
      position: relative;
      color: white;
    }

    &_prop.center {
      display: grid;
      align-content: center;
    }

    &_prop.clickable {
      cursor: pointer;
      position: relative;

      &:hover {
        background-color: #ff000022;
      }
    }

    &_prop.domain {
      cursor: pointer;
      text-align: left;
      text-indent: 1rem;
    }

    &_prop.domain:hover {
      background-color: #00000055;
      .suffix {
        opacity: 1;
        font-size: 1rem;
      }
    }

    &_prop:nth-child(odd) {
      background-color: #00000011;
    }
  }
}

.domains-title {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;

  &_prop {
    width: 100%;
    height: 100%;
    display: grid;
    text-align: center;
    align-content: center;
    border: 1px solid #00000066;
    border-bottom: 3px solid #00000066;
    height: 30px;
    font-size: 1.2rem;
    background-color: #00000011;
  }
}
</style>
